<template>
  <div class="content">
    <div class="info">
      <div class="service">
        <img src="@/assets/personal/yxm.png" alt />
        悦享美客服
        <div class="text">如有任何疑问可添加客服微信 我们将尽力为您解答</div>
      </div>
      <div class="code">
        <img src="@/assets/personal/yxmcode.png" alt />
        <p>长按二维码识别</p>
      </div>
    </div>
    <div class="tel" @click="showPopup">
      <div class="kf">客服电话</div>
      <div class="num">
        020-42003200
        <img src="@/assets/all/arrow_right_ash.png" alt />
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <div class="phone">020-42003200</div>
      <div class="call">呼叫</div>
      <div class="cancel" @click="hidPopup">取消</div>
      <div class="btn">
        <div>
          <div class="btn-l">
            <van-icon name="arrow-left" />
          </div>
          <div class="btn-r">
            <van-icon name="arrow" />
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    hidPopup() {
      this.show = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  font-size: 28px;
  .info {
    padding: 108px 60px;
    background-color: #fff;
    display: flex;
    align-self: center;
    justify-content: space-between;
    .service {
      img {
        width: 120px;
        height: 120px;
        vertical-align: middle;
        margin-right: 16px;
        margin-bottom: 35px;
      }
      .text {
        width: 369px;
        font-size: 26px;
        color: #999;
      }
    }
    .code {
      img {
        width: 240px;
        height: 240px;
      }
      p {
        margin-top: 20px;
        text-align: center;
        color: #888;
        font-size: 24px;
      }
    }
  }
  .tel {
    padding: 0 24px;
    height: 100px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 42px;
    .kf {
      color: #555;
    }
    .num {
      color: #222;
      font-size: 30px;
      img {
        width: 30px;
        height: 30px;
        vertical-align: -3px;
        margin-left: 20px;
      }
    }
  }
  .van-popup {
    background-color: #f2f2f2;
    font-size: 30px;
    .phone {
      background-color: #fff;
      height: 120px;
      line-height: 120px;
      text-align: center;
      border-bottom: thin solid #f5f5f5;
    }
    .call {
      margin-bottom: 10px;
    }
    .cancel,
    .call {
      background-color: #fff;
      height: 100px;
      line-height: 100px;
      text-align: center;
    }
    .btn {
      background-color: #fff;
      height: 89px;
      & > div {
      margin:0 auto;
      height: 100%;
      display: flex;
      align-items: center;
      width:200px;
      justify-content: space-between;
      }
    }
  }
}
</style>



